import { isIOS, isAndroid } from '@/utils/platform'
export default {
  name: 'HeaderNav',
  props: {
    opacity: {
      type: Number,
      default: 0
    }
  },
  methods: {
    goBack () {
      if (isAndroid()) {
        console.log('安卓')
        window.glamor.closePage()
        return
      }
      if (isIOS()) {
        console.log('ios')
        this.$device.JsBridge.callHandler('h5LeftBack', {}, (data) => {})
        return
      }
      console.log('web')
      this.$router.go(-1)
    }
  },
  watch: {
    opacity () {
      return this.opacity
    }
  }
}



