export default {
  name: 'stretch',
  props: {
    title: {
      type: [String, Number],
      default: ''
    },
    content: {
      type: [String, Number],
      default: ''
    }
  },
  data () {
    return {
      isActive: false
    }
  },
  methods: {
    toggle () {
      this.isActive = !this.isActive
      console.log('this.$refs.content.$el.style.lineHeight', this.$refs.content.clientHeight)
    }
  }
}



