import avatar from '@/components/AVATAR'
import imgShowCenter from '@/components/imgShowCenter'
// 引入工具函数判断平台
import { isAndroid, isIOS } from '@/utils/platform'
export default {
  name: 'showGiftList',
  components: {
    avatar,
    imgShowCenter
  },
  props: {
    list: {
      type: Array,
      default: () => []
    }
  },
  mounted () {
    console.log('薪页面')
  },
  methods: {
    // 点击用户头像跳到用户主页
    clickAvatar (uid) {
      if (isAndroid()) {
        window.glamor.clickAvatar(uid.toString())
        return
      }
      if (isIOS()) {
        this.$device.JsBridge.callHandler('h5ClickAvatar', {uid: uid.toString()}, () => {})
      }
    },
    toDetailView (contId, detailUrl, timelineType, content) {
      console.log('contId', contId)
      console.log('detailUrl', detailUrl)
      console.log('timelineType', timelineType)
      console.log('content', content)
      if (!contId || !detailUrl) return
      const isVideo = (timelineType == 1)
      if (isAndroid()) {
        window.glamor.toDetailView(contId, detailUrl, timelineType, content)
        return
      }
      if (isIOS()) {
        this.$device.JsBridge.callHandler('h5ClickPicture', {detailUrl: detailUrl, contId: contId, isVideo: isVideo}, (data) => {})
      }
    }
  }
}



